//////////////////////////////////////////////////////
// STYLES FOR TOGAS-COUTURE-V3 //////////////////////

html, body, header, #intro {
        height: 100%;
    }

#intro {
  background: url("/img/img-togas-lp3/slider1-sh.jpg") no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

/* <!-- SECTION 1.TWO HALF --> *****************************************/
.txt-under {
  font-family: 'PFRegalDisplayPro-MedItalic';
  font-size: 1.6em;
  font-weight: 600;
  line-height: 1.2em;
  color: black;
}


/* <!-- /SECTION 1.TWO HALF --> *****************************************/

.container-header {
  @include wrapper();
}

.row-header {
  @include row-flex();
}


.justify-content-center {
  justify-content: center;
}

.logo {
  @include col();
  @include size(6);
  @include size-lg(7);
  @include size-md(8);
  @include size-sm(12);
}

.sublogo {
  // @include col();
  // @include size-sm(1);
  // background-color: $fff;
}

.offer {
  margin-top: 200px;
  text-align: center;

  &__title {
    font-size: 52px;
    font-weight: 300;
    color: #fff;

    @include sm(font-size, 40px);
    @include xs(font-size, 25px)
  }
}

.offer__form {

  display: flex;
  margin-top: 24px;

  @include xs(flex-direction, column);


  & input {
    background: #fff;
    border: 0;
    width: 328px;
    display: block;
    padding-left: 15px;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 16px;

    @include xs(width, auto);
  }
  & button {
    background: #fd4f32;
    color: #fff;
    font-weight: 700;
    font-size: 16px;
    text-transform: uppercase;
    width: 124px;
    padding: 10px 0;
    cursor: pointer;
    display: block;
    border: none;

    @include xs(width, auto);
  }
}

// third-col
.third-col {
  @include col();
  @include size(4);
  @include size-lg(12);
  @include size-md(12);


}



.col-gap {
  @include col();
  @include size(12);
}



// margins

.row-margin {
  margin-left: 0px;
  margin-right: 0px;
}

